<template>
  <div id="app">
    <div class="login-box">
      <div class="logo">
        <img src="./images/logo.png" height="43" width="199">
      </div>
      <div class="login">
        <div class="username">
          <img src="./images/user.png" height="21" width="21">
          <input type="text" name="" v-model="username" placeholder="请输入用户名">
        </div>
        <div class="password">
          <img src="./images/pwd.png" height="21" width="21">
