        </div>
        <div class="loadbtn">
          <button @click="login">登&nbsp&nbsp&nbsp录</button>
        </div>
      </div>
    </div>
    <m-v-bomb ref='bomb'>
      <span slot="alertContent" style="display:inline-block;margin-top: 33px;">
        <img src="./images/warning.png" height="30" width="30"/><br>
        <span>密码错误，请稍后重试</span>
      </span>
    </m-v-bomb>
  </div>
</template>

