        var config = {
            headers : {
                'Content-Type':'application/x-www-from-urlencoded'
            },
            // withCredentials : true,
            crossDomain:true
        };
        this.$ajax.post("http://121.42.203.85/energycenter/public/api/admin/login",formData,config)
        .then(res => {
            console.log(res.data)
            if(res.data.re==0){
              // location.href='./backStage.html';
            }
            
        })
        .catch(err => {
            console.log(err)
        })



        // var formData = new FormData();
        // formData.append('type','A1002')
        // var config = {
        //     headers : {
        //         'Content-Type':'application/x-www-from-urlencoded'
        //     },
        // };
        // this.$ajax.post("http://121.42.203.85/energycenter/public/api/admin/logOut",formData,config)
        // .then(res => {
        //     console.log(res.data)
        // })
        // .catch(err => {
        //     console.log(err)
        // })
      }
    },
    mounted:function(){

    }
  }
</script>

<style scoped lang='less'>
  #app{
    background-color: #6DA685;
    width:100%;
    height: 100%;
    text-align: center;
    ::-webkit-input-placeholder{
      color:#CACACA;
    }    
    :-moz-placeholder{
      color:#CACACA;
    }              
    ::-moz-placeholder{
      color:#CACACA;
    }                  
    :-ms-input-placeholder{
      color:#CACACA;
    }
    .login-box{
      display: block;
      position: absolute;
      top:40%;
      left: 50%;
      margin-left: -175px;
      margin-top: -153px;
      .logo{
        width:100%;
        text-align: center;
        margin-bottom: 40px;
      }
      .login{
        width:352px;
        margin:auto;
        .username{
            width: 351px;
            height: 50px;
            background-color: #fff;
            border-radius: 40px;
            margin-bottom: 20px;
            position: relative;
            img{
              position: absolute;
              top:14px;
              left: 20px;
            }
            input{
              display: inline-block;
              float: left;
              height: 48px;
              border:none;
              font-size: 18px;
              color:#666553;
              margin-left: 50px;
              padding-left: 15px;
              outline: none;
            }
            /*input::*/
        }
        .password{
          width: 351px;
          height: 50px;
          background-color: #fff;
          border-radius: 40px;
          position: relative;
          margin-bottom: 20px;
          img{
            position: absolute;
            top:14px;
            left: 20px;
          }
          input{
            display: inline-block;
            float: left;
            height: 48px;
            border:none;
            font-size: 18px;
            color:#666553;
            margin-left: 50px;
            padding-left: 15px;
            outline: none;
          }
        }
        .loadbtn{
          button{
            width:351px;
            height: 49px;
            background-color:  #E6E6E6;
            margin-top: 30px;
            outline: none;
            border:none;
            color:#AEAEAE;
            font-size: 20px;
            border-radius: 30px;
            cursor: pointer;
            transition: all 0.2s linear;
            font-weight: 700;
          }
          button:hover{
            color:#333;
            background-color: #F9D8A3;
            box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
          }
        }
      }
    }
    
  }
</style>



